"use client";

import { useState, useEffect } from "react";
import { motion } from "framer-motion";
import { getMultipleSimplePokemon, searchPokemon } from "@/lib/pokeapi";
import type { SimplePokemon, DetailedPokemon } from "@/types/pokemon";
import PokemonGrid from "@/components/PokemonGrid";
import PokemonModal from "@/components/PokemonModal";
import SearchBar from "@/components/SearchBar";
import Hero from "@/components/Hero";

export default function Home() {
  const [pokemon, setPokemon] = useState<SimplePokemon[]>([]);
  const [filteredPokemon, setFilteredPokemon] = useState<SimplePokemon[]>([]);
  const [selectedPokemon, setSelectedPokemon] = useState<DetailedPokemon | null>(null);
  const [loading, setLoading] = useState(true);
  const [searchQuery, setSearchQuery] = useState("");

  // Load initial Pokemon
  useEffect(() => {
    async function loadPokemon() {
      try {
        const data = await getMultipleSimplePokemon(151, 0);
        setPokemon(data);
        setFilteredPokemon(data);
      } catch (error) {
        console.error("Failed to load Pokemon:", error);
      } finally {
        setLoading(false);
      }
    }

    loadPokemon();
  }, []);

  // Handle search
  useEffect(() => {
    if (searchQuery.trim() === "") {
      setFilteredPokemon(pokemon);
    } else {
      const filtered = pokemon.filter((p) =>
        p.name.toLowerCase().includes(searchQuery.toLowerCase())
      );
      setFilteredPokemon(filtered);
    }
  }, [searchQuery, pokemon]);

  return (
    <main className="min-h-screen bg-gradient-to-b from-slate-50 to-slate-100 dark:from-slate-900 dark:to-slate-800">
      {/* Hero Section */}
      <Hero onSearchChange={setSearchQuery} />

      {/* Main Content */}
      <section className="container mx-auto px-4 sm:px-6 lg:px-8 pb-24">
        {/* Search Results Counter */}
        {searchQuery && (
          <motion.div
            initial={{ opacity: 0, y: -10 }}
            animate={{ opacity: 1, y: 0 }}
            className="mb-8 text-center"
          >
            <p className="text-lg text-slate-600 dark:text-slate-400">
              Found <span className="font-bold text-slate-900 dark:text-white">{filteredPokemon.length}</span>{" "}
              Pokémon matching &quot;{searchQuery}&quot;
            </p>
          </motion.div>
        )}

        {/* Loading State */}
        {loading && (
          <div className="flex items-center justify-center py-32">
            <div className="text-center">
              <div className="inline-block h-16 w-16 animate-spin rounded-full border-4 border-solid border-blue-500 border-r-transparent align-[-0.125em] motion-reduce:animate-[spin_1.5s_linear_infinite]" role="status">
                <span className="sr-only">Loading Pokémon...</span>
              </div>
              <p className="mt-4 text-xl font-semibold text-slate-700 dark:text-slate-300">
                Loading Pokémon...
              </p>
            </div>
          </div>
        )}

        {/* Pokemon Grid */}
        {!loading && filteredPokemon.length > 0 && (
          <PokemonGrid
            pokemon={filteredPokemon}
            onPokemonClick={setSelectedPokemon}
          />
        )}

        {/* No Results */}
        {!loading && filteredPokemon.length === 0 && searchQuery && (
          <motion.div
            initial={{ opacity: 0, scale: 0.95 }}
            animate={{ opacity: 1, scale: 1 }}
            className="flex flex-col items-center justify-center py-32"
          >
            <div className="text-center">
              <div className="text-6xl mb-4">😢</div>
              <h3 className="text-2xl font-bold text-slate-900 dark:text-white mb-2">
                No Pokémon Found
              </h3>
              <p className="text-slate-600 dark:text-slate-400">
                Try searching for a different Pokémon
              </p>
            </div>
          </motion.div>
        )}
      </section>

      {/* Pokemon Modal */}
      {selectedPokemon && (
        <PokemonModal
          pokemon={selectedPokemon}
          onClose={() => setSelectedPokemon(null)}
        />
      )}
    </main>
  );
}
