"use client";

import { motion } from "framer-motion";
import Image from "next/image";
import { SimplePokemon } from "@/types/pokemon";
import { TYPE_COLORS } from "@/types/pokemon";

interface PokemonCardProps {
  pokemon: SimplePokemon;
  onClick: () => void;
  index: number;
}

export default function PokemonCard({ pokemon, onClick, index }: PokemonCardProps) {
  // Get primary type for gradient
  const primaryType = pokemon.types[0];
  const primaryColor = TYPE_COLORS[primaryType] || "#A8A77A";

  // Lighten color for gradient
  const lightenColor = (color: string) => {
    const r = parseInt(color.slice(1, 3), 16);
    const g = parseInt(color.slice(3, 5), 16);
    const b = parseInt(color.slice(5, 7), 16);
    return `rgba(${r}, ${g}, ${b}, 0.1)`;
  };

  return (
    <motion.div
      initial={{ opacity: 0, y: 20 }}
      animate={{ opacity: 1, y: 0 }}
      transition={{ duration: 0.3, delay: index * 0.02 }}
      whileHover={{ y: -8, scale: 1.02 }}
      whileTap={{ scale: 0.98 }}
      className="group"
    >
      <button
        onClick={onClick}
        aria-label={`View details for ${pokemon.name}`}
        className="w-full h-full bg-white dark:bg-slate-800 rounded-2xl shadow-lg hover:shadow-2xl transition-all duration-300 overflow-hidden focus:outline-none focus:ring-4 focus:ring-blue-500 focus:ring-offset-2"
        style={{
          background: `linear-gradient(135deg, ${lightenColor(primaryColor)} 0%, white 100%)`,
        }}
      >
        {/* Card Content */}
        <div className="p-6 flex flex-col items-center">
          {/* Pokemon Number */}
          <div className="self-end mb-2">
            <span className="text-xs font-bold text-slate-400 dark:text-slate-500">
              #{pokemon.id.toString().padStart(3, "0")}
            </span>
          </div>

          {/* Pokemon Image */}
          <div className="relative w-32 h-32 mb-4">
            <Image
              src={pokemon.sprite}
              alt={pokemon.name}
              fill
              className="object-contain transition-transform duration-300 group-hover:scale-110"
              priority={index < 12}
            />

            {/* Legendary/Mythical Badge */}
            {(pokemon.isLegendary || pokemon.isMythical) && (
              <motion.div
                initial={{ scale: 0 }}
                animate={{ scale: 1 }}
                transition={{ delay: 0.2 }}
                className="absolute -top-2 -right-2 bg-gradient-to-br from-yellow-400 to-yellow-600 rounded-full p-2 shadow-lg"
                aria-label={pokemon.isLegendary ? "Legendary Pokémon" : "Mythical Pokémon"}
              >
                <span className="text-lg" aria-hidden="true">
                  {pokemon.isLegendary ? "⭐" : "✨"}
                </span>
              </motion.div>
            )}
          </div>

          {/* Pokemon Name */}
          <h3 className="text-xl font-bold text-slate-900 dark:text-white capitalize mb-3 text-center">
            {pokemon.name}
          </h3>

          {/* Pokemon Types */}
          <div className="flex gap-2 flex-wrap justify-center">
            {pokemon.types.map((type) => (
              <span
                key={type}
                className="px-3 py-1 rounded-full text-xs font-semibold text-white shadow-md transition-transform duration-200 group-hover:scale-105"
                style={{ backgroundColor: TYPE_COLORS[type] }}
              >
                {type.charAt(0).toUpperCase() + type.slice(1)}
              </span>
            ))}
          </div>
        </div>

        {/* Hover Effect Overlay */}
        <div className="absolute inset-0 bg-gradient-to-t from-slate-900/10 to-transparent opacity-0 group-hover:opacity-100 transition-opacity duration-300 pointer-events-none" />
      </button>
    </motion.div>
  );
}
