"use client";

import { useState, useEffect } from "react";
import { motion, AnimatePresence } from "framer-motion";
import Image from "next/image";
import { DetailedPokemon, TYPE_COLORS } from "@/types/pokemon";

interface PokemonModalProps {
  pokemon: DetailedPokemon;
  onClose: () => void;
}

type SpriteType = "normal" | "shiny" | "normalFemale" | "shinyFemale" | "officialArtwork" | "officialArtworkShiny";

export default function PokemonModal({ pokemon, onClose }: PokemonModalProps) {
  const [activeSprite, setActiveSprite] = useState<SpriteType>("officialArtwork");
  const [currentSpriteUrl, setCurrentSpriteUrl] = useState<string>(pokemon.sprite);

  // Update sprite when type changes
  useEffect(() => {
    const spriteMap: Record<SpriteType, string | null> = {
      normal: pokemon.sprites.normal,
      shiny: pokemon.sprites.shiny,
      normalFemale: pokemon.sprites.normalFemale,
      shinyFemale: pokemon.sprites.shinyFemale,
      officialArtwork: pokemon.sprites.officialArtwork,
      officialArtworkShiny: pokemon.sprites.officialArtworkShiny,
    };

    setCurrentSpriteUrl(spriteMap[activeSprite] || pokemon.sprite);
  }, [activeSprite, pokemon]);

  // Handle ESC key
  useEffect(() => {
    const handleEsc = (e: KeyboardEvent) => {
      if (e.key === "Escape") onClose();
    };
    document.addEventListener("keydown", handleEsc);
    return () => document.removeEventListener("keydown", handleEsc);
  }, [onClose]);

  // Prevent body scroll
  useEffect(() => {
    document.body.style.overflow = "hidden";
    return () => {
      document.body.style.overflow = "unset";
    };
  }, []);

  const primaryType = pokemon.types[0];
  const primaryColor = TYPE_COLORS[primaryType] || "#A8A77A";

  // Available sprite options
  const spriteOptions: { type: SpriteType; label: string; available: boolean }[] = [
    { type: "officialArtwork", label: "Official", available: !!pokemon.sprites.officialArtwork },
    { type: "officialArtworkShiny", label: "Shiny", available: !!pokemon.sprites.officialArtworkShiny },
    { type: "normal", label: "Normal", available: !!pokemon.sprites.normal },
    { type: "shiny", label: "Shiny Sprite", available: !!pokemon.sprites.shiny },
    { type: "normalFemale", label: "Female", available: !!pokemon.sprites.normalFemale },
    { type: "shinyFemale", label: "Shiny Female", available: !!pokemon.sprites.shinyFemale },
  ];

  return (
    <AnimatePresence>
      <div
        className="fixed inset-0 z-50 overflow-y-auto"
        aria-labelledby="modal-title"
        role="dialog"
        aria-modal="true"
      >
        {/* Backdrop */}
        <motion.div
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          exit={{ opacity: 0 }}
          onClick={onClose}
          className="fixed inset-0 bg-slate-900/75 backdrop-blur-sm transition-opacity"
          aria-hidden="true"
        />

        {/* Modal Container */}
        <div className="flex min-h-full items-center justify-center p-4">
          <motion.div
            initial={{ opacity: 0, scale: 0.95, y: 20 }}
            animate={{ opacity: 1, scale: 1, y: 0 }}
            exit={{ opacity: 0, scale: 0.95, y: 20 }}
            transition={{ duration: 0.2 }}
            className="relative w-full max-w-6xl bg-white dark:bg-slate-800 rounded-3xl shadow-2xl overflow-hidden"
          >
            {/* Close Button */}
            <button
              onClick={onClose}
              aria-label="Close modal"
              className="absolute top-4 right-4 z-10 p-2 rounded-full bg-slate-100 dark:bg-slate-700 hover:bg-slate-200 dark:hover:bg-slate-600 transition-colors focus:outline-none focus:ring-2 focus:ring-blue-500"
            >
              <svg
                className="w-6 h-6 text-slate-600 dark:text-slate-300"
                fill="none"
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth="2"
                viewBox="0 0 24 24"
                stroke="currentColor"
              >
                <path d="M6 18L18 6M6 6l12 12"></path>
              </svg>
            </button>

            {/* Header with gradient */}
            <div
              className="relative px-8 pt-8 pb-24"
              style={{
                background: `linear-gradient(135deg, ${primaryColor}dd 0%, ${primaryColor}88 100%)`,
              }}
            >
              <div className="flex items-start justify-between mb-6">
                <div>
                  <p className="text-white/80 text-sm font-semibold mb-1">
                    #{pokemon.id.toString().padStart(3, "0")}
                  </p>
                  <h2
                    id="modal-title"
                    className="text-4xl sm:text-5xl font-black text-white capitalize"
                  >
                    {pokemon.name}
                  </h2>
                  <p className="text-white/90 text-lg mt-2">{pokemon.genus}</p>
                </div>

                {/* Badges */}
                <div className="flex gap-2">
                  {pokemon.isLegendary && (
                    <span className="px-3 py-1 bg-yellow-400 text-yellow-900 rounded-full text-sm font-bold">
                      ⭐ Legendary
                    </span>
                  )}
                  {pokemon.isMythical && (
                    <span className="px-3 py-1 bg-purple-400 text-purple-900 rounded-full text-sm font-bold">
                      ✨ Mythical
                    </span>
                  )}
                </div>
              </div>

              {/* Types */}
              <div className="flex gap-2 flex-wrap">
                {pokemon.types.map((type) => (
                  <span
                    key={type}
                    className="px-4 py-2 rounded-full text-sm font-bold text-white shadow-lg"
                    style={{ backgroundColor: TYPE_COLORS[type] }}
                  >
                    {type.charAt(0).toUpperCase() + type.slice(1)}
                  </span>
                ))}
              </div>

              {/* Pokemon Image - Overlapping */}
              <div className="absolute -bottom-32 left-1/2 -translate-x-1/2 w-64 h-64">
                <div className="relative w-full h-full bg-white dark:bg-slate-700 rounded-3xl shadow-2xl p-6">
                  <Image
                    src={currentSpriteUrl}
                    alt={pokemon.name}
                    fill
                    className="object-contain p-4"
                  />
                </div>
              </div>
            </div>

            {/* Content */}
            <div className="px-8 pt-40 pb-8 space-y-8">
              {/* Sprite Toggle */}
              <div>
                <h3 className="text-lg font-bold text-slate-900 dark:text-white mb-3">
                  Sprite Variants
                </h3>
                <div className="flex gap-2 flex-wrap">
                  {spriteOptions
                    .filter((option) => option.available)
                    .map((option) => (
                      <button
                        key={option.type}
                        onClick={() => setActiveSprite(option.type)}
                        className={`px-4 py-2 rounded-lg text-sm font-semibold transition-all ${
                          activeSprite === option.type
                            ? "bg-blue-500 text-white shadow-lg"
                            : "bg-slate-100 dark:bg-slate-700 text-slate-700 dark:text-slate-300 hover:bg-slate-200 dark:hover:bg-slate-600"
                        }`}
                      >
                        {option.label}
                      </button>
                    ))}
                </div>
              </div>

              {/* Description */}
              <div>
                <h3 className="text-lg font-bold text-slate-900 dark:text-white mb-2">
                  Description
                </h3>
                <p className="text-slate-600 dark:text-slate-300 leading-relaxed">
                  {pokemon.description}
                </p>
              </div>

              {/* Physical Info */}
              <div className="grid grid-cols-2 gap-4">
                <div className="bg-slate-50 dark:bg-slate-700/50 rounded-xl p-4">
                  <p className="text-sm text-slate-500 dark:text-slate-400 font-semibold mb-1">
                    Height
                  </p>
                  <p className="text-2xl font-bold text-slate-900 dark:text-white">
                    {pokemon.height} m
                  </p>
                </div>
                <div className="bg-slate-50 dark:bg-slate-700/50 rounded-xl p-4">
                  <p className="text-sm text-slate-500 dark:text-slate-400 font-semibold mb-1">
                    Weight
                  </p>
                  <p className="text-2xl font-bold text-slate-900 dark:text-white">
                    {pokemon.weight} kg
                  </p>
                </div>
              </div>

              {/* Abilities */}
              <div>
                <h3 className="text-lg font-bold text-slate-900 dark:text-white mb-3">
                  Abilities
                </h3>
                <div className="flex gap-2 flex-wrap">
                  {pokemon.abilities.map((ability) => (
                    <span
                      key={ability}
                      className="px-4 py-2 bg-slate-100 dark:bg-slate-700 text-slate-900 dark:text-white rounded-lg text-sm font-medium capitalize"
                    >
                      {ability.replace("-", " ")}
                    </span>
                  ))}
                </div>
              </div>

              {/* Stats */}
              <div>
                <h3 className="text-lg font-bold text-slate-900 dark:text-white mb-4">
                  Base Stats
                </h3>
                <div className="space-y-3">
                  {pokemon.stats.map((stat) => {
                    const percentage = (stat.value / 255) * 100;
                    return (
                      <div key={stat.name}>
                        <div className="flex justify-between mb-1">
                          <span className="text-sm font-medium text-slate-600 dark:text-slate-400 capitalize">
                            {stat.name.replace("-", " ")}
                          </span>
                          <span className="text-sm font-bold text-slate-900 dark:text-white">
                            {stat.value}
                          </span>
                        </div>
                        <div className="w-full bg-slate-200 dark:bg-slate-700 rounded-full h-2">
                          <motion.div
                            initial={{ width: 0 }}
                            animate={{ width: `${percentage}%` }}
                            transition={{ duration: 0.5, delay: 0.1 }}
                            className="h-2 rounded-full"
                            style={{ backgroundColor: primaryColor }}
                          />
                        </div>
                      </div>
                    );
                  })}
                </div>
              </div>

              {/* Evolution Chain */}
              {pokemon.evolutionChain.length > 1 && (
                <div>
                  <h3 className="text-lg font-bold text-slate-900 dark:text-white mb-4">
                    Evolution Chain
                  </h3>
                  <div className="flex flex-wrap items-center gap-4 justify-center md:justify-start">
                    {pokemon.evolutionChain.map((evo, index) => (
                      <div key={evo.id} className="flex items-center gap-4">
                        {/* Evolution Card */}
                        <div className="flex flex-col items-center">
                          <div
                            className={`relative w-24 h-24 bg-slate-100 dark:bg-slate-700 rounded-xl p-2 ${
                              evo.id === pokemon.id
                                ? "ring-4 ring-blue-500"
                                : ""
                            }`}
                          >
                            <Image
                              src={evo.sprite}
                              alt={evo.name}
                              fill
                              className="object-contain"
                            />
                          </div>
                          <p className="text-sm font-semibold text-slate-900 dark:text-white mt-2 capitalize">
                            {evo.name}
                          </p>
                          {evo.level && (
                            <p className="text-xs text-slate-500 dark:text-slate-400">
                              Lv. {evo.level}
                            </p>
                          )}
                          {evo.item && (
                            <p className="text-xs text-slate-500 dark:text-slate-400 capitalize">
                              {evo.item.replace("-", " ")}
                            </p>
                          )}
                        </div>

                        {/* Arrow */}
                        {index < pokemon.evolutionChain.length - 1 && (
                          <svg
                            className="w-6 h-6 text-slate-400"
                            fill="none"
                            strokeLinecap="round"
                            strokeLinejoin="round"
                            strokeWidth="2"
                            viewBox="0 0 24 24"
                            stroke="currentColor"
                            aria-hidden="true"
                          >
                            <path d="M13 7l5 5m0 0l-5 5m5-5H6"></path>
                          </svg>
                        )}
                      </div>
                    ))}
                  </div>
                </div>
              )}

              {/* Alternative Forms */}
              {pokemon.forms.length > 1 && (
                <div>
                  <h3 className="text-lg font-bold text-slate-900 dark:text-white mb-4">
                    Alternative Forms
                  </h3>
                  <div className="grid grid-cols-2 sm:grid-cols-3 md:grid-cols-4 gap-4">
                    {pokemon.forms.map((form) => (
                      <div
                        key={form.name}
                        className="flex flex-col items-center bg-slate-50 dark:bg-slate-700/50 rounded-xl p-4"
                      >
                        <div className="relative w-20 h-20 mb-2">
                          <Image
                            src={form.sprite}
                            alt={form.name}
                            fill
                            className="object-contain"
                          />
                        </div>
                        <p className="text-xs font-medium text-slate-900 dark:text-white text-center capitalize">
                          {form.name.replace("-", " ")}
                        </p>
                        {form.isDefault && (
                          <span className="text-xs text-blue-500 font-semibold mt-1">
                            Default
                          </span>
                        )}
                      </div>
                    ))}
                  </div>
                </div>
              )}
            </div>
          </motion.div>
        </div>
      </div>
    </AnimatePresence>
  );
}
