"use client";

import { SimplePokemon, DetailedPokemon } from "@/types/pokemon";
import { getDetailedPokemon } from "@/lib/pokeapi";
import PokemonCard from "./PokemonCard";
import { useState } from "react";

interface PokemonGridProps {
  pokemon: SimplePokemon[];
  onPokemonClick: (pokemon: DetailedPokemon) => void;
}

export default function PokemonGrid({ pokemon, onPokemonClick }: PokemonGridProps) {
  const [loadingId, setLoadingId] = useState<number | null>(null);

  const handleClick = async (simplePokemon: SimplePokemon) => {
    setLoadingId(simplePokemon.id);
    try {
      const detailed = await getDetailedPokemon(simplePokemon.id);
      onPokemonClick(detailed);
    } catch (error) {
      console.error("Failed to load detailed Pokemon:", error);
    } finally {
      setLoadingId(null);
    }
  };

  return (
    <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4 xl:grid-cols-5 2xl:grid-cols-6 gap-6">
      {pokemon.map((p, index) => (
        <PokemonCard
          key={p.id}
          pokemon={p}
          onClick={() => handleClick(p)}
          index={index}
        />
      ))}
    </div>
  );
}
