// Type definitions for Pokemon data from PokeAPI

export interface Pokemon {
  id: number;
  name: string;
  height: number;
  weight: number;
  types: PokemonType[];
  sprites: PokemonSprites;
  stats: PokemonStat[];
  abilities: PokemonAbility[];
  species: {
    name: string;
    url: string;
  };
}

export interface PokemonType {
  slot: number;
  type: {
    name: string;
    url: string;
  };
}

export interface PokemonSprites {
  front_default: string | null;
  front_shiny: string | null;
  front_female: string | null;
  front_shiny_female: string | null;
  back_default: string | null;
  back_shiny: string | null;
  other: {
    "official-artwork": {
      front_default: string | null;
      front_shiny: string | null;
    };
    home: {
      front_default: string | null;
      front_shiny: string | null;
      front_female: string | null;
      front_shiny_female: string | null;
    };
    showdown: {
      front_default: string | null;
      front_shiny: string | null;
      front_female: string | null;
      front_shiny_female: string | null;
    };
  };
}

export interface PokemonStat {
  base_stat: number;
  effort: number;
  stat: {
    name: string;
    url: string;
  };
}

export interface PokemonAbility {
  ability: {
    name: string;
    url: string;
  };
  is_hidden: boolean;
  slot: number;
}

export interface PokemonSpecies {
  id: number;
  name: string;
  evolution_chain: {
    url: string;
  };
  flavor_text_entries: FlavorTextEntry[];
  genera: Genera[];
  varieties: Variety[];
  color: {
    name: string;
  };
  shape: {
    name: string;
  };
  is_legendary: boolean;
  is_mythical: boolean;
}

export interface FlavorTextEntry {
  flavor_text: string;
  language: {
    name: string;
    url: string;
  };
  version: {
    name: string;
    url: string;
  };
}

export interface Genera {
  genus: string;
  language: {
    name: string;
    url: string;
  };
}

export interface Variety {
  is_default: boolean;
  pokemon: {
    name: string;
    url: string;
  };
}

export interface EvolutionChain {
  id: number;
  chain: ChainLink;
}

export interface ChainLink {
  species: {
    name: string;
    url: string;
  };
  evolution_details: EvolutionDetail[];
  evolves_to: ChainLink[];
}

export interface EvolutionDetail {
  min_level: number | null;
  trigger: {
    name: string;
    url: string;
  };
  item: {
    name: string;
    url: string;
  } | null;
  held_item: {
    name: string;
    url: string;
  } | null;
  known_move: {
    name: string;
    url: string;
  } | null;
  min_happiness: number | null;
  min_beauty: number | null;
  min_affection: number | null;
  time_of_day: string;
  location: {
    name: string;
    url: string;
  } | null;
}

export interface PokemonListResponse {
  count: number;
  next: string | null;
  previous: string | null;
  results: PokemonListItem[];
}

export interface PokemonListItem {
  name: string;
  url: string;
}

// Simplified types for UI components
export interface SimplePokemon {
  id: number;
  name: string;
  types: string[];
  sprite: string;
  isLegendary?: boolean;
  isMythical?: boolean;
}

export interface DetailedPokemon extends SimplePokemon {
  height: number;
  weight: number;
  stats: { name: string; value: number }[];
  abilities: string[];
  description: string;
  genus: string;
  evolutionChain: EvolutionNode[];
  forms: PokemonForm[];
  sprites: SpriteCollection;
}

export interface EvolutionNode {
  id: number;
  name: string;
  sprite: string;
  types: string[];
  level?: number;
  trigger?: string;
  item?: string;
}

export interface PokemonForm {
  name: string;
  sprite: string;
  isDefault: boolean;
}

export interface SpriteCollection {
  normal: string | null;
  shiny: string | null;
  normalFemale: string | null;
  shinyFemale: string | null;
  officialArtwork: string | null;
  officialArtworkShiny: string | null;
}

